export const renderPrice = oldPrice => {
  const price = oldPrice / 1000000;
  if( parseInt(price) >= 1 ) {
    if(price.toString().includes('.')) {
      return `${parseFloat(price).toFixed(1)} . .`;
    } else {
      return `${parseInt(price)} . .`;
    }
  } else {
    return `${parseInt(price*1000)} . .`;
  }
}

export const renderArea = area => {
  if( parseFloat(area) >= 1 ) {
    return `${parseFloat(area).toFixed(1)} `;
  } else {
    return `${parseInt(area * 100)} `;
  }
}

export const renderCoords = coords => {
  return `${coords.split(',')[0]}, ${coords.split(',')[1]}`;
}


